.App {
  text-align: center;
}

a {
  text-decoration: inherit;
  color: inherit;
}

body {
  font-family: Gilroy-Regular;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 40px;
  height: 100%;
  &-s {
    max-width: 600px;
    margin: 0 auto;
    padding: 0 40px;
    height: 100%;
  }
  &-m {
    max-width: 800px;
    margin: 0 auto;
    padding: 0 40px;
    height: 100%;
  }
  &-l {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 40px;
    height: 100%;
  }
  &-xl {
    max-width: 1600px;
    margin: 0 auto;
    padding: 0 40px;
    height: 100%;
  }
}

.move-left-icon {
  transform: rotateZ(180deg);
}

.note {
  font-family: "Gilroy-LightItalic";
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

@-moz-keyframes spin {
  from {
    -moz-transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
